/*
--Keyframes Slider - Lg
*/
@keyframes slider-lg {
  0% {
    background-image: url(../img/banner-1.png); }
  33% {
    background-image: url(../img/banner-2.png); }
  66% {
    background-image: url(../img/banner-3.png); }
  100% {
    background-image: url(../img/banner-1.png); } }

@keyframes fade-out {
  27% {
    opacity: 0; }
  33% {
    opacity: 1; }
  60% {
    opacity: 1; }
  66% {
    opacity: 0; } }

ul.kf-slider li {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  z-index: -4;
  opacity: 0; }

ul.kf-slider li:nth-child(1) {
